import { Grid, Box } from "@material-ui/core";
import { useEffect, useState } from "react";
import Chart from "react-google-charts";
import DatePickers from "./datePickers";
import Button from "@mui/material/Button";
import BackButton from "../Common/BackButton";

const DatosHistoricos = () => {
  const [datos, setDatos] = useState([]);

  function toCandlestick(RowNames) {
    let auxArr = [];
    auxArr[0] = RowNames;
    var i = 1;
    datos.forEach((item) => {
      let dateItem = new Date(item._id);
      auxArr[i++] = [dateItem, item.high, item.close, item.open, item.low];
    });
    return auxArr;
  }

  function download (){
      var a = document.createElement("a");
      var file = new Blob([JSON.stringify(datos)], {type: "text/plain"});
      a.href = URL.createObjectURL(file);
      a.download = "data.json";
      a.click();  
  }

  const dibujarGreafica = () => {
    if (datos.length > 1)
      return (
        <Box sx={{ width: "95vw" }}>
          <Chart
            width={"auto"}
            height={"400px"}
            chartType="CandlestickChart"
            loader={<div>Loading Chart</div>}
            data={toCandlestick(["day", "HIGH", "CLOSE", "OPEN", "LOW"])}
            options={{
              hAxis: {
                title: "Date",
              },
              vAxis: {
                title: "Price",
              },
              series: {},
              title: "EURUSD Data",
              explorer: {
                axis: "horizontal",
                keepInBounds: true,
                maxZoomIn: 25.0,
              },
            }}
            rootProps={{ "data-testid": "2" }}
          />
          <Box sx={{ mx: "auto", width: "100px" }}>
            <Button variant="contained" onClick={download}>Descargar</Button>
          </Box>
        </Box>
      );
  };
  useEffect(() => {});
  return (
    <Grid
      container
      direction="column"
      alignItems="center"
      justifyContent="center"
    >
      <BackButton/>
      <h2>Consulta de datos</h2>
      <DatePickers datos={datos} setDatos={setDatos} />
      {dibujarGreafica()}
    </Grid>
  );
};
export default DatosHistoricos;
